/**
 * Preprocessing Types - V0.8
 * 
 */

/**  */
export type PreprocessingMode =
    | 'query_enhance'    // RAG Query 
    | 'plot_director'    // 
    | 'description'      // 
    | 'custom';          // 

/**  */
export interface PreprocessingConfig {
    /**  */
    enabled: boolean;
    /**  ID */
    templateId: string;
    /**  () */
    autoTrigger: boolean;
}

/**  */
export interface PreprocessingResult {
    /**  */
    success: boolean;
    /** <output>  -  */
    output: string | null;
    /** <query>  - RAG  (Query ) */
    query: string | null;
    /**  LLM  ( <think>) */
    rawOutput: string;
    /**  ID (Query ) */
    recalledIds?: string[];
    /**  (ms) */
    processingTime: number;
    /**  */
    error?: string;
}

/**  */
export const DEFAULT_PREPROCESSING_CONFIG: PreprocessingConfig = {
    enabled: false,
    templateId: 'query_enhance',
    autoTrigger: true,
};
