/**
 * ProcessingView - 
 *
 *  Tab 
 *  APIPresetsView 
 */
import React, { useState } from 'react';
import { FileText, Database, Layers, Boxes, ScrollText, BookOpen, Search } from 'lucide-react';
import { Tab } from "@/components/ui/TabPills";
import { LayoutTabs } from "@/components/layout/LayoutTabs";
import { Divider } from "@/components/layout/Divider";
import { QuickLinks, QuickLink } from '@/components/common/QuickLinks';
import { SummaryPanel } from './SummaryPanel';
import { VectorizationPanel } from './VectorizationPanel';
import { RecallPanel } from './RecallPanel';

type ProcessingTab = 'summary' | 'vectorization' | 'recall' | 'batch';

//  Tab 
const MAIN_TABS: Tab[] = [
    { id: 'summary', label: '', icon: <FileText size={16} /> },
    { id: 'vectorization', label: '', icon: <Database size={16} /> },
    { id: 'recall', label: '', icon: <Search size={16} /> },
    { id: 'batch', label: '', icon: <Layers size={16} /> },
];

//  page:subtab 
const QUICK_LINKS: QuickLink[] = [
    { id: 'devlog', label: '', icon: ScrollText, linkTo: 'devlog:model' },
    { id: 'presets', label: '', icon: BookOpen, linkTo: 'presets:prompt' },
];

interface ProcessingViewProps {
    onNavigate?: (path: string) => void;
}

export const ProcessingView: React.FC<ProcessingViewProps> = ({ onNavigate }) => {
    const [activeTab, setActiveTab] = useState<ProcessingTab>('summary');

    return (
        <div className="flex flex-col h-full">
            {/*  -  +  */}
            <div className="mb-6">
                <h1 className="text-2xl font-light text-foreground tracking-tight mb-2"></h1>
                <p className="text-sm text-muted-foreground"> RAG </p>
            </div>
            <Divider className="mb-6" />

            {/*  -  Portal  Header */}
            <LayoutTabs
                tabs={MAIN_TABS}
                activeTab={activeTab}
                onChange={(id) => setActiveTab(id as ProcessingTab)}
                actions={
                    <QuickLinks
                        links={QUICK_LINKS}
                        onNavigate={(path) => onNavigate?.(path)}
                    />
                }
            />

            {/*  */}
            <div className="flex-1 overflow-y-auto no-scrollbar">
                {/*  Tab -  SummaryPanel  */}
                {activeTab === 'summary' && <SummaryPanel />}

                {/*  Tab - V0.7  VectorizationPanel */}
                {activeTab === 'vectorization' && <VectorizationPanel />}

                {/*  Tab - V0.8.5 */}
                {activeTab === 'recall' && <RecallPanel />}

                {/*  Tab */}
                {activeTab === 'batch' && (
                    <div className="flex flex-col items-center justify-center h-48 text-muted-foreground gap-2">
                        <Boxes size={32} strokeWidth={1} className="opacity-30" />
                        <p className="text-sm font-light">...</p>
                    </div>
                )}
            </div>
        </div>
    );
};

export default ProcessingView;
