/**
 * WorldBookSlotService - WorldBook 
 *
 *  {{engramSummaries}} 
 *  WorldBook 
 */

import { WorldInfoService } from '@/tavern/api/WorldInfo';
import { Logger } from '@/lib/logger';

/**  */
const SLOT_ENTRY_KEY = 'engram_macro_slot';
const SLOT_ENTRY_NAME = 'Engram Memory Context';

/**
 * WorldBookSlotService 
 */
export class WorldBookSlotService {
    private static isInitialized = false;

    /**
     *  WorldBook 
     * 
     */
    static async init(): Promise<void> {
        if (this.isInitialized) return;

        try {
            const worldbookName = await WorldInfoService.getOrCreateWorldbook();
            if (!worldbookName) {
                Logger.warn('WorldBookSlotService', '');
                return;
            }

            // 
            const existingEntry = await WorldInfoService.findEntryByKey(worldbookName, SLOT_ENTRY_KEY);

            if (existingEntry) {
                Logger.debug('WorldBookSlotService', '', {
                    uid: existingEntry.uid,
                    name: existingEntry.name
                });
                this.isInitialized = true;
                return;
            }

            // 
            /**
             * 
             * - position: 'at_depth' + depth: 999  999
             * - role: 'system'  
             * - constant: true  
             * - content:  {{engramSummaries}} 
             */
            const success = await WorldInfoService.createEntry(worldbookName, {
                name: SLOT_ENTRY_NAME,
                content: '{{engramSummaries}}',
                keys: [SLOT_ENTRY_KEY],
                constant: true,
                enabled: true,
                position: 'at_depth',
                role: 'system',
                depth: 999,
                order: 9000,  //  order
            });

            if (success) {
                Logger.success('WorldBookSlotService', '', {
                    worldbook: worldbookName,
                    name: SLOT_ENTRY_NAME
                });
                this.isInitialized = true;
            } else {
                Logger.error('WorldBookSlotService', '');
            }

        } catch (e) {
            Logger.error('WorldBookSlotService', '', e);
        }
    }

    /**
     * 
     */
    static isReady(): boolean {
        return this.isInitialized;
    }

    /**
     * 
     */
    static reset(): void {
        this.isInitialized = false;
    }
}
