import React, { useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import { TabPills, TabPillsProps } from '../ui/TabPills';

/**
 * LayoutTabs - 
 *
 *  TabPills  MainLayout  Header  (Portal)
 *  TabPills  Header 
 *
 * :  View 
 */
export const LayoutTabs: React.FC<TabPillsProps> = (props) => {
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
        return () => setMounted(false);
    }, []);

    const container = document.getElementById('engram-header-extension');

    //  ( fallback)
    if (!mounted || !container) {
        return null;
    }

    //  HeaderExtension  Header 
    // 1.  sticky ( fixed/flex header )
    // 2.  (mb-6)  (HeaderExtension )
    // 3.  (HeaderExtension )
    // 4.  Layout  (px-4 md:px-8...)
    const headerProps = {
        ...props,
        sticky: false,
        className: `!mb-0 !border-0 !bg-transparent px-4 md:px-8 lg:px-12 ${props.className || ''}`
    };

    return createPortal(
        <TabPills {...headerProps} />,
        container
    );
};
