  /**  */
  outputFormat: 'json' | 'markdown' | 'plain';
  /** : 'replace'=, 'append'=, 'prepend'= */
  injectionMode?: 'replace' | 'append' | 'prepend';
  /**  */
  availableVariables: string[];
  /**  */
  createdAt: number;
  /**  */
