/**
 *  LLM 
 */
export function createDefaultLLMPreset(name: string = ''): LLMPreset {
  const now = Date.now();
  return {
    id: `preset_${now}`,
    name,
    source: 'tavern',
    parameters: { ...DEFAULT_SAMPLING_PARAMETERS },
    context: { ...DEFAULT_CONTEXT_SETTINGS },
    isDefault: true,
    createdAt: now,
    updatedAt: now,
  };
}

/**
 * 
 */
export function createPromptTemplate(
  name: string,
  category: PromptCategory,
  options: Partial<Omit<PromptTemplate, 'name' | 'category' | 'createdAt' | 'updatedAt'>> & { id?: string } = {}
): PromptTemplate {
  const now = Date.now();
  return {
    id: options.id || `template_${now}_${Math.random().toString(36).slice(2, 8)}`,
    name,
    category,
    enabled: options.enabled ?? false,
    isBuiltIn: options.isBuiltIn ?? false,
    boundPresetId: options.boundPresetId ?? null,
    systemPrompt: options.systemPrompt ?? '',
    userPromptTemplate: options.userPromptTemplate ?? '',
    outputFormat: options.outputFormat ?? 'plain',
    availableVariables: options.availableVariables ?? ['{{chatHistory}}', '{{context}}', '{{char}}', '{{user}}'],
    createdAt: now,
    updatedAt: now,
  };
}

/**
 * 
 */
export function getBuiltInPromptTemplates(): PromptTemplate[] {
  return [
    createPromptTemplate('', 'summary', {
      id: 'builtin_summary',
      enabled: true,
      isBuiltIn: true,
      systemPrompt: summaryPrompt,
      userPromptTemplate: `{{worldbookContext}}


{{chatHistory}}

---
 JSON `,
      outputFormat: 'json',
    }),
    createPromptTemplate('', 'trim', {
      id: 'builtin_trim',
      enabled: true,
      isBuiltIn: true,
      systemPrompt: trimPrompt,
      userPromptTemplate: `{{worldbookContext}}




{{engramSummaries}}

`,
      outputFormat: 'json',
    }),
    createPromptTemplate('Query ', 'preprocessing', {
      id: 'builtin_query_enhance',
      enabled: true,
      isBuiltIn: true,
      systemPrompt: queryEnhancePrompt,
      userPromptTemplate: `****:
{{worldbookContext}}

****:
{{chatHistory}}

****:
{{userInput}}

---
`,
      outputFormat: 'plain',
      availableVariables: ['{{worldbookContext}}', '{{chatHistory}}', '{{userInput}}', '{{char}}', '{{user}}'],
    }),
    createPromptTemplate('', 'preprocessing', {
      id: 'builtin_plot_director',
      enabled: false,  // 
      isBuiltIn: true,
      systemPrompt: plotDirectorPrompt,
      userPromptTemplate: `****:
{{worldbookContext}}

****:
{{chatHistory}}

****:
{{userInput}}

---
`,
      outputFormat: 'plain',
      injectionMode: 'append', //  AI 
      availableVariables: ['{{worldbookContext}}', '{{context}}', '{{chatHistory}}', '{{userInput}}', '{{char}}', '{{user}}'],
    }),
    createPromptTemplate('', 'preprocessing', {
      id: 'builtin_description_enhance',
      enabled: false,  // 
      isBuiltIn: true,
      systemPrompt: descriptionPrompt,
      userPromptTemplate: `****:
{{chatHistory}}

****:
{{userInput}}

---
`,
      outputFormat: 'plain',
      injectionMode: 'replace', // 
      availableVariables: ['{{context}}', '{{chatHistory}}', '{{userInput}}', '{{char}}', '{{user}}'],
    }),
  ];
}

/**
 *  ID 
 * @param id  ID
 * @returns  undefined
 */
export function getBuiltInTemplateById(id: string): PromptTemplate | undefined {
  return getBuiltInPromptTemplates().find(t => t.id === id);
}

/**
 * 
 * @param category 
 * @returns  null
 */
export function getBuiltInTemplateByCategory(category: PromptCategory): PromptTemplate | null {
  return getBuiltInPromptTemplates().find(t => t.category === category) || null;
}


