/**
 * LLMAdapter - LLM 
 *
 *  SillyTavern/TavernHelper LLM API 
 *
 *  SummarizerRAGGraph 
 */

/** LLM  */
export interface LLMRequest {
    /**  */
    systemPrompt: string;
    /**  */
    userPrompt: string;
    /**  ID */
    presetId?: string;
}

/** LLM  */
export interface LLMResponse {
    /**  */
    content: string;
    /**  */
    success: boolean;
    /**  */
    error?: string;
    /** Token  */
    tokenUsage?: {
        prompt: number;
        completion: number;
        total: number;
    };
}

/**
 *  TavernHelper API
 */
function getTavernHelper(): {
    generate?: (options: unknown) => Promise<string>;
    generateRaw?: (options: unknown) => Promise<string>;
} | null {
    try {
        // @ts-expect-error - TavernHelper 
        return window.TavernHelper || null;
    } catch {
        return null;
    }
}

/**
 * LLMAdapter 
 *  LLM 
 */
export class LLMAdapter {
    /**
     *  LLM 
     * @param request 
     */
    async generate(request: LLMRequest): Promise<LLMResponse> {
        const helper = getTavernHelper();

        if (!helper?.generateRaw && !helper?.generate) {
            return {
                success: false,
                content: '',
                error: 'TavernHelper ',
            };
        }

        try {
            let content: string;

            // =========================================================================
            // Context Aggregation Gateway (V0.8 Upgrade)
            // =========================================================================
            // 
            // 1. :  ST-Prompt-Template (EJS)  Regex 
            // 2. : Native Pipeline -> Engram Pipeline

            // [Step 1]  (Simulation)
            //  User 
            const combinedPrompt = request.systemPrompt ?
                `${request.systemPrompt}\n\n${request.userPrompt}` :
                request.userPrompt;

            // 
            const generateData = {
                prompt: combinedPrompt, // 
                body: {
                    messages: [
                        { role: 'system', content: request.systemPrompt },
                        { role: 'user', content: request.userPrompt }
                    ]
                },
                // 
                is_engram: true, //  ()
            };

            // [Step 2]  Native Pipeline Hook ( ST-Prompt-Template)
            //  EJS  Regex
            const { EventBus, TavernEventType } = await import('@/tavern/api');
            await EventBus.emit(TavernEventType.GENERATE_AFTER_DATA, generateData);

            // [Step 3] 
            // ST-Prompt-Template  generateData.prompt
            const processedPrompt = typeof generateData.prompt === 'string' ?
                generateData.prompt : combinedPrompt;

            // 
            //  prompt  User Trigger 
            // ( system prompt )
            //  processedPrompt  system prompt ( step 1 )
            //  API  system prompt

            let finalSystemPrompt = request.systemPrompt;
            let finalUserPrompt = request.userPrompt;

            if (processedPrompt !== combinedPrompt) {
                //  EJS/Regex 
                //  prompt 
                //  prompt 
                //  System Prompt User Prompt  (Raw Mode)
                finalSystemPrompt = '';
                finalUserPrompt = processedPrompt;
            }

            // [Step 4]  Engram Pipeline (RegexProcessor)
            //  Engram  <think> 
            // 
            const { regexProcessor } = await import('@/services/pipeline/RegexProcessor');
            finalUserPrompt = regexProcessor.process(finalUserPrompt, 'input');

            // =========================================================================
            // End of Gateway
            // =========================================================================

            if (helper.generateRaw) {
                //  generateRaw 
                const prompts = [];
                if (finalSystemPrompt) {
                    prompts.push({ role: 'system', content: finalSystemPrompt });
                }
                prompts.push({ role: 'user', content: finalUserPrompt });

                content = await helper.generateRaw({
                    ordered_prompts: prompts,
                });
            } else if (helper.generate) {
                // fallback:  generate
                content = await helper.generate({
                    user_input: finalUserPrompt,
                    system_prompt: finalSystemPrompt,
                    should_stream: false,
                    max_chat_history: 0,
                });
            } else {
                throw new Error(' API');
            }

            return {
                success: true,
                content: content || '',
            };
        } catch (e) {
            const errorMsg = e instanceof Error ? e.message : String(e);
            console.error('[Engram] LLMAdapter :', e);

            return {
                success: false,
                content: '',
                error: errorMsg,
            };
        }
    }

    /**
     *  LLM API 
     */
    isAvailable(): boolean {
        const helper = getTavernHelper();
        return !!(helper?.generate || helper?.generateRaw);
    }

    /**
     *  Token 
     * @param text 
     */
    estimateTokens(text: string): number {
        //  2 /token 4 /token
        //  3
        return Math.ceil(text.length / 3);
    }
}

/**  */
export const llmAdapter = new LLMAdapter();

export default LLMAdapter;
