/**
 * RerankService - Rerank API 
 *
 * V0.8.5: 
 *  OpenAI  Rerank API
 */

import { SettingsManager } from '@/services/settings/Persistence';
import { Logger } from '@/lib/logger';
import type { RerankConfig } from '@/services/api/types';

// ====================  ====================

/**
 * Rerank 
 */
export interface RerankResultItem {
    /**  */
    index: number;
    /**  ( 0-1) */
    relevance_score: number;
}

/**
 * Rerank API 
 */
interface RerankAPIResponse {
    results?: RerankResultItem[];
    data?: RerankResultItem[];
}

// ==================== RerankService ====================

export class RerankService {
    private static instance: RerankService;

    private constructor() { }

    static getInstance(): RerankService {
        if (!RerankService.instance) {
            RerankService.instance = new RerankService();
        }
        return RerankService.instance;
    }

    /**
     *  Rerank 
     */
    private getConfig(): RerankConfig | null {
        const apiSettings = SettingsManager.get('apiSettings');
        return apiSettings?.rerankConfig || null;
    }

    /**
     *  Rerank 
     */
    isEnabled(): boolean {
        const config = this.getConfig();
        return !!(config?.enabled && config.url && config.model);
    }

    /**
     *  Rerank API 
     *
     * @param query 
     * @param documents 
     * @returns 
     */
    async rerank(query: string, documents: string[]): Promise<RerankResultItem[]> {
        const config = this.getConfig();

        if (!config?.enabled) {
            Logger.debug('RerankService', 'Rerank ');
            return documents.map((_, i) => ({ index: i, relevance_score: 0 }));
        }

        if (!config.url || !config.model) {
            Logger.warn('RerankService', 'Rerank ', { url: config.url, model: config.model });
            return documents.map((_, i) => ({ index: i, relevance_score: 0 }));
        }

        if (documents.length === 0) {
            return [];
        }

        try {
            //  API 
            const endpoint = config.url.endsWith('/rerank')
                ? config.url
                : `${config.url.replace(/\/$/, '')}/rerank`;

            Logger.debug('RerankService', ' Rerank API', {
                endpoint,
                model: config.model,
                queryLength: query.length,
                documentCount: documents.length,
            });

            const response = await fetch(endpoint, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    ...(config.apiKey && {
                        'Authorization': `Bearer ${config.apiKey}`
                    })
                },
                body: JSON.stringify({
                    model: config.model,
                    query,
                    documents,
                    top_n: config.topN || 5,
                }),
            });

            if (!response.ok) {
                const errorText = await response.text();
                throw new Error(`Rerank API  (${response.status}): ${errorText}`);
            }

            const data: RerankAPIResponse = await response.json();

            //  API 
            const results = data.results || data.data || [];

            Logger.info('RerankService', 'Rerank ', {
                resultCount: results.length,
                topScore: results[0]?.relevance_score,
            });

            // 
            return results.sort((a, b) => b.relevance_score - a.relevance_score);

        } catch (error) {
            const errorMsg = error instanceof Error ? error.message : '';
            Logger.error('RerankService', 'Rerank ', { error: errorMsg });

            // 
            return documents.map((_, i) => ({ index: i, relevance_score: 0 }));
        }
    }

    /**
     *  (alpha)
     * 0 = 1 =  Rerank 
     */
    getHybridAlpha(): number {
        const config = this.getConfig();
        return config?.hybridAlpha ?? 0.5;
    }
}

/**  */
export const rerankService = RerankService.getInstance();
